
import { defineComponent } from 'vue'

export default defineComponent({
  name: '',
  components: {},
  props: {
    tableData: {
      type: Array,
      require: true
    },
    columns: {
      type: Array,
      require: true
    },
    showIndexColumn: {
      type: Boolean,
      default: false
    },
    showSelectColumn: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    }
  },
  emits: ['selectionChange'],
  setup(props, { emit }) {
    console.log('props: ', props)
    function selectionChange(value: any): void {
      // 不是通过返回 进行给父组件传递值
      emit('selectionChange', value)
    }
    const currentPage1 = 2
    const handleSizeChange = () => {
      return {}
    }
    const handleCurrentChange = () => {
      return {}
    }
    return {
      selectionChange,
      currentPage1,
      handleSizeChange,
      handleCurrentChange
    }
  }
})
